












































import AddMeToHome from './AddMeToHome.vue';
import store from '../store';

export default {
  name: 'Stat',
  components: { AddMeToHome },
  computed: {
    categories: () => store.categories,
  },
  methods: {
    setCategory: (id: string) => store.setCategory(id),
    showPage: (name: string) => store.showPage(name),
  },
};
